.receipt-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.receipt-modal {
  background: white;
  border-radius: 12px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.receipt-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 2px solid #f0f0f0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 12px 12px 0 0;
}

.receipt-header h2 {
  margin: 0;
  font-size: 24px;
  font-weight: 600;
}

.close-btn {
  background: none;
  border: none;
  font-size: 28px;
  color: white;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background-color 0.2s;
}

.close-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.receipt-content {
  padding: 24px;
}

.store-info {
  text-align: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px dashed #ddd;
}

.store-info h3 {
  margin: 0 0 8px 0;
  font-size: 20px;
  color: #333;
}

.store-info p {
  margin: 4px 0;
  color: #666;
  font-size: 14px;
}

.order-info {
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px dashed #ddd;
}

.order-info p {
  margin: 8px 0;
  color: #333;
  font-size: 14px;
}

.receipt-items {
  margin-bottom: 24px;
}

.receipt-items h4 {
  margin: 0 0 16px 0;
  color: #333;
  font-size: 16px;
  font-weight: 600;
}

.items-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 12px;
  padding: 12px 0;
  border-bottom: 2px solid #eee;
  font-weight: 600;
  color: #555;
  font-size: 14px;
}

.receipt-item {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 12px;
  padding: 12px 0;
  border-bottom: 1px solid #f5f5f5;
  font-size: 14px;
}

.item-name {
  color: #333;
  font-weight: 500;
}

.item-qty, .item-price, .item-total {
  text-align: right;
  color: #666;
}

.receipt-totals {
  margin-bottom: 24px;
  padding: 16px;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.total-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  font-size: 14px;
  color: #333;
}

.final-total {
  margin-top: 12px;
  padding-top: 12px;
  border-top: 2px solid #ddd;
  font-size: 18px;
  color: #2c3e50;
}

.payment-selection {
  margin-bottom: 24px;
}

.payment-selection h4 {
  margin: 0 0 16px 0;
  color: #333;
  font-size: 16px;
  font-weight: 600;
}

.payment-methods {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 12px;
}

.payment-method-btn {
  padding: 12px 16px;
  border: 2px solid #e0e0e0;
  background: white;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.payment-method-btn:hover {
  border-color: #667eea;
  background-color: #f8f9ff;
}

.payment-method-btn.active {
  border-color: #667eea;
  background-color: #667eea;
  color: white;
}

.receipt-actions {
  display: flex;
  gap: 12px;
  padding: 20px 24px;
  border-top: 2px solid #f0f0f0;
  background-color: #f8f9fa;
  border-radius: 0 0 12px 12px;
}

.print-btn, .complete-order-btn {
  flex: 1;
  padding: 14px 20px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.print-btn {
  background-color: #6c757d;
  color: white;
}

.print-btn:hover {
  background-color: #5a6268;
  transform: translateY(-1px);
}

.complete-order-btn {
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
  color: white;
}

.complete-order-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #218838 0%, #1ea085 100%);
  transform: translateY(-1px);
}

.complete-order-btn:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
  transform: none;
}

/* Print styles */
@media print {
  .receipt-modal-overlay {
    position: static;
    background: none;
  }
  
  .receipt-modal {
    box-shadow: none;
    border-radius: 0;
    max-width: none;
    width: 100%;
    max-height: none;
  }
  
  .receipt-header {
    background: none !important;
    color: black !important;
    border-bottom: 2px solid black;
  }
  
  .close-btn, .payment-selection, .receipt-actions {
    display: none !important;
  }
  
  .receipt-content {
    padding: 0;
  }
}

/* Responsive design */
@media (max-width: 768px) {
  .receipt-modal {
    width: 95%;
    margin: 20px;
  }
  
  .receipt-header {
    padding: 16px 20px;
  }
  
  .receipt-content {
    padding: 20px;
  }
  
  .items-header, .receipt-item {
    grid-template-columns: 2fr 0.8fr 0.8fr 1fr;
    gap: 8px;
    font-size: 13px;
  }
  
  .payment-methods {
    grid-template-columns: 1fr;
  }
  
  .receipt-actions {
    flex-direction: column;
    padding: 16px 20px;
  }
}